import React from 'react';
import './css/style.css';
import './css/bootstrap.css';
import {
  // HashRouter as Router,
  BrowserRouter as Router,
  Route,
  // Link
} from 'react-router-dom'

import { Index } from './components/Index.js';
import { Navbar } from './components/module/Navbar.js';
import { Footer } from './components/module/Footer.js';
import { Search } from './components/Search.js';
import { Contribution } from './components/Contribution.js';
import { Show as ArticleShow } from './components/article/Show.js';
import { Notice } from './components/Notice.js';
import { Login } from './components/Login.js';
import { Register } from './components/Register.js';

import { Home as Auth } from './components/auth/Home.js';
import { Info } from './components/auth/Info.js';
import { Collection } from './components/auth/Collection.js';
import { History } from './components/auth/History.js';


const Main = () => (
  <Router>
    <div>
      <Navbar />
      <div className="container" style={{ minHeight: '500px' }}>
        <Route exact path="/" component={Index} />
        <Route path="/article/all/" component={Index} />
        <Route path="/article/tag/:id" component={Index} />

        <Route path="/article/show/:id" component={ArticleShow} />
        <Route path="/notice/show/:id" component={Notice} />
        <Route path="/search/:keywords" component={Search} />
        <Route path="/contribution" component={Contribution} />
        <Route path="/login" component={Login} />
        <Route path="/register" component={Register} />

        <Route path="/auth/info" component={Info} />
        <Route path="/auth/collection" component={Collection} />
        <Route path="/auth/history" component={History} />
        <Route path="/auth/home" component={Auth} />

      </div>
      <Footer />
    </div>
  </Router >
)
export default Main



