import React from 'react';
import { Sidebar } from './module/Sidebar.js';
import { Route } from 'react-router-dom';
import { Home } from './Home.js';
import { ShowByTag } from './ShowByTag.js';

/**嵌套路由的父路由index 
 * 因为首页，分类显示页，文章所有页的布局基本一致。
 * 只是数据不一样而已，
 * 所有做一个index父组件，抽出公有的右侧栏组件。
 * **/
const Index = ({ match }) => (
	<div className="row">
		<Route path="/article/all/" component={Home} />
		<Route path="/article/tag/:id" component={ShowByTag} />
		<Route exact path="/" component={Home} />

		<Sidebar />
	</div>
);

export { Index }



