import React from 'react';
import autoBind from 'react-autobind';
import { Sidebar } from './module/Sidebar.js';
import { store } from '../reducers';
import { getArticles } from '../functions/getArticles';
import VisibleArticleList from '../containers/VisibleArticleList.js';


class Search extends React.Component {
  constructor(props) {
    super(props);
    autoBind(this);
    this.state = {
      url: window.location.pathname
    };

  }

  componentWillMount() {
    console.log(this.props.match.params.keywords);
    getArticles('article/search/' + this.props.match.params.keywords);
  }

  componentWillReceiveProps(nextProps) {
    //监听路由变化，如果路由和原来的url不一样，手动获取数据，并更新url
    if (this.state.url !== nextProps.location.pathname) {
      getArticles('article/search/' + this.props.match.params.keywords);
      this.setState({ url: nextProps.location.pathname });
    }

  }

  componentDidMount() {

  }

  render() {
    return (
      <div className="row">
        <div className="col-md-8">
          <VisibleArticleList store={store} />
        </div>
        <Sidebar />
      </div>
    );
  }
}

export { Search }



